import { Component, Host, Prop, h } from '@stencil/core';
import { TpColor } from '../../../../../components';
import { navigate } from '../../../../../utils/functions/navigation.function';
import { NEWS_SEARCH } from './utils/news-search.constant';
import { CONFIG } from '../../../../../config/config';

@Component({
  tag: 'tp-elem-news-search',
  styleUrl: 'news-search.scss',
  scoped: true,
  assetsDirs: ['assets'],
})
export class TpElemNewsSearch {
  /**
   * Define a variação de cor do componente.
   */
  @Prop({ reflect: true }) color: TpColor = NEWS_SEARCH.defaultColor;

  /**
   * Define a rota base da página de pesquisa por termo.
   */
  @Prop({ reflect: true }) baseSearchPath = '/';

  /**
   * Define a rota base da página de categoria.
   */
  @Prop({ reflect: true }) baseCategoryPath = '/';

  onSearchByTerm(event: CustomEvent) {
    navigate(
      `${this.baseSearchPath}?${NEWS_SEARCH.searchKey}=${event.detail.value}`,
    );
  }

  onFilterByCategory(event: CustomEvent) {
    navigate(`${this.baseCategoryPath}/${event.detail.value}`);
  }

  render() {
    const { color } = this;

    console.log('ENV:', CONFIG);
    console.log('mode:', process.env.NODE_ENV);

    return (
      <Host class="tp-news-search">
        <div class="tp-news-search__container">
          <tp-input-container color={color} class="tp-news-search__term-search">
            <ion-searchbar
              mode="md"
              search-icon={NEWS_SEARCH.icons.search}
              clear-icon={NEWS_SEARCH.icons.x}
              placeholder="Buscar"
              onIonChange={(e) => this.onSearchByTerm(e)}
            ></ion-searchbar>
          </tp-input-container>

          <tp-input-container
            color={color}
            class="tp-news-search__category-filter"
          >
            <ion-select
              placeholder="Selecione uma categoria"
              interface="popover"
              interfaceOptions={NEWS_SEARCH.interfaceOptions}
              onIonChange={(e) => this.onFilterByCategory(e)}
            >
              <slot name="select-options"></slot>
            </ion-select>
          </tp-input-container>
        </div>
      </Host>
    );
  }
}
